ul {
  margin-bottom: -1em;
}

li {
  color: #fff;
  font-optical-sizing: auto;
  margin-bottom: 24px;
  font-family: Work Sans, sans-serif;
  font-size: max(1.2rem, min(3vw, 1.2rem));
  font-style: normal;
  font-weight: 400;
}

body {
  flex-direction: column;
  min-width: 100vw;
  height: 100vh;
  display: flex;
}

nav {
  background-color: var(--green);
}

main {
  flex-grow: 1;
  flex-basis: 0;
  width: 100%;
  overflow-y: auto;
}

.stripes {
  width: 100vw;
  height: 72px;
  margin-left: calc(0px - var(--x-padding));
  background: url("stripes.c032f86a.svg");
}

.footer-text {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 24px;
  display: flex;
}

.footer-text p {
  margin-top: 0;
}

.down-arrow {
  background: url("arrow-down.7508adba.svg") center / 100% 100% no-repeat;
  width: 26px;
  height: 37px;
}

section {
  padding-left: var(--x-padding);
  padding-right: var(--x-padding);
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-height: 100%;
  display: flex;
}

section header h1 {
  margin-top: 1em;
  font-size: max(2rem, min(7vw, 3rem));
}

section header p {
  margin-top: 32px;
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

section h2 {
  color: #fff;
  margin-top: 32px;
  margin-bottom: 0;
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

section p {
  margin-top: 24px;
  font-size: max(1.2rem, min(3vw, 1.2rem));
}

section .footer p {
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

section .stripes {
  margin-top: 24px;
}

#section-2 .arrows-section {
  flex-direction: column;
  align-self: center;
  align-items: center;
  width: 100%;
  max-width: 500px;
  margin-top: 2em;
  margin-bottom: 1em;
  display: flex;
}

#section-2 .arrows-section p {
  margin-top: 0;
}

#section-2 .arrows-section .align-left {
  text-align: left;
  align-self: flex-start;
}

#section-2 .arrows-section .align-right {
  text-align: left;
  align-self: flex-end;
}

#section-2 .swish-section {
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
  margin-bottom: 1em;
  display: flex;
}

#swish-link {
  width: 100%;
}

#swish-view p {
  max-width: 240px;
  margin-top: 1em;
  font-size: max(.5rem, min(2.5vw, 1rem));
}

#swish-view button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 240px;
  height: 50px;
  margin-top: 1em;
  padding: .5em 1em;
  display: inline-flex;
  box-shadow: 0 1px 3px #0003;
}

#swish-view button img {
  height: 32px;
  display: block;
}

#section-1 {
  background-color: var(--green);
}

#section-2 {
  background-color: var(--blue);
}

#section-3 {
  background-color: var(--red);
}

#section-4 {
  background-color: var(--green);
}
